<template>
    <div class="project">

      <headeR />

      <h1>Nethanzêl's projects.</h1>

      <h3>If you want to get any of these projects that is not on Github or as a service, see the 
        <router-link to="/market">marketplace</router-link> page.
      </h3>

      <section class="projectCont" v-for="(project, i) in projects" :key="i">

        <img :src="projects[i].icon" alt="project logo" class="projectLogo">

        <h2 class="projName">{{projects[i].title}}</h2>

        <p class="projDesc">
          {{projects[i].desc}}
        </p>

        <div class="projDet">
          <div class="projTech">
            <p>Made using:</p>

            <div class="projTech_cont" v-for="(tool, x) in projects[i].tools" :key="x">

              <div class="iconCont">
                <img :src="getIcon(projects[i].tools[x])" class="skillIcon">
                <p>{{projects[i].tools[x]}}</p>
              </div>

            </div>
          </div>

          <div class="projFooter" v-if="projects[i].open">
            <p>Get this porject on Github</p>
            <a target="_blank" :href="projects[i].source">{{projects[i].source}}</a>
          </div>
        </div>
      </section>

    </div>
</template>

<script>
import headeR from '@/components/navegationMenu.vue'
import axios from 'axios'

export default {
  name: "Projects",
  title: "Nethanzêl's Showroom", 
  components: {
    headeR
  },

  data() {
    return {
      projects: {},
      tools: {}
    }
  },

  methods: {

    getProjects() {
      axios.get("/api/projects", {headers: {token: this.client()}})
        .then(res => {
          if (res.status == 200) {
            this.projects = res.data
          }
        })
    },

    getSkills() {
      axios.get("/api/skills", {headers: {token: this.client()}})
        .then(res => {
          this.tools = res.data[0].technical
        })
    },

    getIcon(name) {

      for(let i = 0; i < this.tools.known.length; i++) {
        if (this.tools.known[i][1] == name) {
          return this.tools.known[i][0]
        }
      }

      for(let i = 0; i < this.tools.diving.length; i++) {
        if (this.tools.diving[i][1] == name) {
          return this.tools.diving[i][0]
        }
      }
    }
  },

  mounted() {
    this.getSkills()
    this.getProjects()
  }
}
</script>

<style scoped lang="scss">

.project {
  mix-blend-mode: difference;
  will-change: opacity;
  padding-top: 130px;
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: default;
  margin-bottom: 50px;
}

.projectCont {
  border: 2px solid #fff;
  width: 75%;
  height: auto;
  padding: 15px 10px;

  display: flex;
  flex-direction: column;
  align-items: center;

  background: #99999944;
  margin: 20px 0;
}

h1 {
  letter-spacing: 3px;
  position: fixed;
  padding-top: 60px;
  padding-bottom: 10px;
  text-align: center;
  width: 100%;
  background: #000;
  top: 0;
  cursor: default;
  z-index: 10;
}

//====================================================
.projectLogo {
  height: 200px;
  width: 200px;
  border: 2px solid #fff;
  border-radius: 50%;
  object-fit: cover;
  isolation: isolate;
  background: #fff;
  z-index: 0;
  object-fit: scale-down;
}

.projFooter {
  border-top: 1px solid #fff;
  margin-top: 15px;
  padding: 5px 15px;
  width: 100%;
  min-width: 235px;
  overflow: hidden;

  display: flex;
  flex-direction: column;
  align-items: center;
}

.projFooter p {
  margin-bottom: 8px;
}

.projFooter a, a {
  text-decoration: none;
  color: #000;
  background: #fff;
  padding: 1px 7px;
  font-weight: bold;
}

.projName {
  margin: 5px 0 10px 0; 
  font-size: 35px;
}

.projDesc {
  text-align: justify;
  margin: 0 10px 0 10px;
}

.projTech {
  border-top: 1px solid #fff;
  margin-top: 30px;
  padding: 5px 15px;
  min-width: 235px;
  width: 100%;
}

.projTech_cont {
  padding-top: 5px;
}

.projDet {
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

//====================================================

.skillIcon {
  height: 60px;
  width: 60px;
  margin: 0 10px;
  border-radius: 50%;
  background: #fff;
  object-fit: cover;
  transition: opacity .7s;
}

.iconCont {
  display: inline-flex;
  margin: 10px 10px;
  max-width: 100px;
  justify-content: center;
  align-items: center;
}

.iconCont p {
  position: absolute;
  opacity: 0;
  cursor: default;
  font-weight: bold;
  font-size: 17px;
  text-overflow: clip;
  transition: opacity 1s;
}

.iconCont:hover p {
  opacity: 1;
  transition: opacity .5s;
}

.iconCont:hover .skillIcon {
  opacity: .3;
  transition: opacity .5s;
}

