import Vue from 'vue'
import App from './App.vue'
import router from './router'
import axios from 'axios'
import vueAxios from 'vue-axios'
import titleMix from "./mixins/titleMix"

Vue.use(vueAxios, axios)

Vue.mixin({
  data : function() {
    return {
      client: () => {
        let auth = sessionStorage.getItem("showroom")
        if(auth) {
          return auth
        } else {
          axios.get("http://127.0.0.1/auth")
            .then(res => {
              sessionStorage.setItem("showroom", res.headers.token)
              return res.headers.token
            })
        }
      }
    }
  }
})

Vue.mixin(titleMix)
Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
