<template>
  <div class="skill">
    <headeR />
    <h1>Nethanzêl's skills.</h1>

    <div class="skillCont topDiv">
      <!-- ======================================= -->
      <div class="technical">
        <h2>Technical knowledge of:</h2>

        <div class="iconCont" v-for="(skill, i) in technical.known" :key="i">
          <img :src="technical.known[i][0]" class="skillIcon">
          <p>{{technical.known[i][1]}}</p>
        </div>

      </div>
        <!-- ======================================= -->
      <div class="diving">
        <h2>Diving into:</h2>

        <div class="iconCont" v-for="(skill, i) in technical.diving" :key="i">
          <img :src="technical.diving[i][0]" class="skillIcon">
          <p>{{technical.diving[i][1]}}</p>
        </div>
      </div>
        <!-- ======================================= -->
    </div>

    <div class="skillCont">
      <!-- ======================================= -->
      <div class="technical">
        <h2>Working abilities:</h2>

        <div class="iconCont" v-for="(skill, i) in working.working" :key="i">
