</template>

<script>
import headeR from '@/components/navegationMenu.vue'
import axios from 'axios'
export default {
  name: "Skills",
  title: "Nethanzêl's Showroom", 
  components: {
    headeR
  },

  data()  {
    return {
      technical: {},
      working: {},
      studies: {}
    }
  },

  methods: {

    getSkills() {
      axios.get("/api/skills", {headers: {token: this.client()}})
        .then(res => {
          if(res.status == 200) {
            this.technical = res.data[0].technical
            this.working = res.data[0].working
            this.studies = res.data[0].studies
          }
        })
    }
  },

  mounted() {
    this.getSkills()
  }

}
</script>

